body {
  background-color: #f4f4f3;
  padding: 50px;
}

.gallery {
  flex-wrap: wrap;
  gap: 30px;
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
}

.photo-card {
  width: 450px;
  border-radius: 10px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(213, 217, 217, .5);
}

.info {
  background-color: #fff;
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.photo-container {
  height: 300px;
}

.last-page {
  color: #8e1616;
  font-size: 16px;
}

button {
  box-sizing: border-box;
  color: #0f1111;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  background-color: #fff;
  border: 1px solid #d5d9d9;
  border-radius: 8px;
  padding: 0 10px;
  font-size: 13px;
  line-height: 29px;
  text-decoration: none;
  box-shadow: 0 2px 5px rgba(213, 217, 217, .5);
}

button:hover {
  background-color: #f7fafa;
}

button:focus {
  border-color: #008296;
  outline: 0;
  box-shadow: 0 2px 5px rgba(213, 217, 217, .5);
}

.search-input {
  box-sizing: border-box;
  color: #0f1111;
  width: 300px;
  vertical-align: middle;
  background-color: #fff;
  border: 1px solid #d5d9d9;
  border-radius: 8px;
  padding: 0 10px 0 11px;
  font-size: 13px;
  line-height: 29px;
  box-shadow: 0 2px 5px rgba(213, 217, 217, .5);
}

